@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body{
    font-family: 'Raleway', sans-serif;
    background: #000000;
}
h1{
    font-size: 2rem;
}
#quote-box{
    background-color: #ffffff;
    width:40rem;
    margin: 10rem auto;
    padding: 3rem;
    border-radius: 5px;
}

#text{
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

#character{
    display: flex;
    justify-content: flex-end;
    font-size: 1.2rem;
}

#buttons{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.5rem;
}

#new-quote{
    background: #444444;
    border: none;
    color: white;
    padding: .7rem 1rem;
    font-size: .9rem;
    font-weight: bold;
    border-radius: 2px;
    outline: none;
    cursor: pointer;
}

#new-quote:hover{
    opacity: 0.9;
}

@media (max-width: 654px){
   
    h1{
        font-size: 1.5rem;
    }
    #quote-box {
      width: 30rem;
    }

    #text{
        font-size: 1.5rem;
    }
  }

  @media (max-width:540px){

    h1{
        font-size: 1.5rem;
    }
    #quote-box {
      width: 25rem;
    }

    #text{
        font-size: 1.1rem;
    }
    #character{
        font-size: 1rem;
    }
  }

  @media (max-width:480px){

    h1{
        font-size: 1.3rem;
        margin-bottom: 10px;
    }
    #quote-box {
      width: 18rem;
    }

    #text{
        font-size: 1.05rem;
    }
    #character{
        
        font-size: 0.955rem;
    }
  }
